.porject2{
    background:linear-gradient(#069AD6 10%, white 30%, white);
    overflow: hidden;
    h2{
        margin-top: 6.250vw;
    }
    article{
        width: 90%;
        margin: 0 auto;
        .issue{
            margin-top: 150px;
            .issue_container{
                display: flex;
                justify-content: space-between;
            }
            dl{
                width: 55%;
             &:nth-child(1){
                width: 40%;
             }
                dt{
                    margin-bottom: 10px;
                }
                dd{
                    line-height: 1.5;
                    img{
                     width: 2000px;
                    }
                }
            }
        }
        .project_box{
            border: 5px solid black;
            width: 100%;
        }
        h3{
            margin-bottom: 1.5vw;
            font-size: 1.563vw;
            font-family: 'NeoDunggeunmo';
        }
        div{
            margin: 2.5vw 0;
        }
        .flex_contaner{
            display: flex;
            div{
                width: 50%;
            }
        }
        .completion{
            margin-top: 150px;
            h3{
                text-align: center;
            }
            .project_box{
                width: 70%;
                margin: 0 auto;
            }
        }
    }

}